@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");

footer {
  background-color: #f4eae0;
  padding: 20px;
  color: #1e1e1e;
  font-family: "Roboto", sans-serif;
  position: relative;
  width: 100%;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
  z-index: 1000;
}

.footer-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 2rem;
  padding: 1rem;
}

.footer-links ul {
  list-style: none;
  margin: 0;
  justify-content: left;
  padding: 0;
}

.footer-links ul li {
  margin-bottom: 8px;
  font-weight: 500;
}

.footer-links a {
  text-decoration: none;
  color: #2b2d42;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: #ef6d57;
}

.footer-links a::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #ef6d57;
  transition: width 0.3s;
}

.footer-links a:hover::after {
  width: 100%;
}

.footer-info {
  max-width: 300px;
  text-align: center;
  font-size: 14px;
}

.footer-info h3 {
  margin-bottom: 10px;
  color: #2b2d42;
}

.footer-info p {
  margin-bottom: 8px;
  color: #555;
  font-weight: 400;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-top: 10px;
}

.social-icons a {
  color: #2b2d42;
  font-size: 2rem;
  padding: 8px;
  border-radius: 50%;
  height: 50px;
  background-color: #ef6d5795;
  transition: all 0.3s ease;
}

.social-icons a:hover {
  color: #ef6d57;
  background-color: #1e1e1e;
}

.footer-copy {
  margin-top: 1rem;
  font-size: 0.85rem;
  text-align: center;
  color: #6c757d;
}

@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-info {
    text-align: center;
  }

  .social-icons {
    justify-content: center;
  }
}
